body {
  margin: 0; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.overflow-hidden {
  overflow: hidden; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

