.page-body__layout {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh; }

.header {
  flex-shrink: 0;
  background-color: #1B2D37; }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  max-width: 1920px;
  margin: 0 auto;
  padding: 33px 13%; }

.header__logo {
  margin: 0 20px 0 0; }

.header__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none; }

.header__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 20px 0 0; }
  .header__item:last-child {
    margin: 0 0 0 0; }

.header__link {
  font-family: "PT Mono", "Courier New", Courier, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff; }

.header__link--email,
.header__link--phone {
  margin: 0 0 0 16px; }

.header__link--button {
  display: block;
  width: 180px;
  height: 45px;
  padding: 15px 20px 17px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 5px; }

.main {
  flex-grow: 1;
  min-height: 50px;
  background-color: red; }

.footer {
  flex-shrink: 0;
  min-height: 50px;
  background-color: blue; }
